import { messages } from './../../pages/extra-components/chat/messages';
import { UserAuth, UserData } from './../../@core/data/users';
import { Component, OnInit, OnChanges } from '@angular/core';
import { Router } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { AuthenticationService } from '../Auth.Service';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { userAuthenticate } from './UserAuthenticate';
@Component({
    selector: 'nb-login',
    styleUrls: ['./login.component.scss'],
    templateUrl: './login.component.html',
})
export class LoginComponent implements OnInit  {

  UserForme: FormGroup;
  usenameMessages:any;
  pwdMessages:any;
  loginMessages:any;

  constructor(private router:Router,private http:HttpClient,private fb: FormBuilder,private authService:AuthenticationService ){ 
    this.loginMessages=null;
    this.authService.isLogged=false;
  }
signin(userData){

 this.http.post<any>('http://localhost:3000/rfq/login',userData).subscribe(result=>{
    if(result.token!=null)
   
 {localStorage.setItem('token', result.token);
   this.loginMessages="Login successfully";
 this.authService.isLogged=true;
var valeurs =window.localStorage.getItem("token");
var base64Url = valeurs.split('.')[1];
  var base64 = base64Url.replace('-', '+').replace('_', '/');
 var currentrole =JSON.parse(window.atob(base64));
this.authService.Role=currentrole;
 
 this.authService.currentUser=userData['userName'];
 

 this.router.navigate(['pages']) 
 ;}
 else if (result.message!=null)

 {this.usenameMessages=result.message;}
 else this.pwdMessages=result.pwdmessage;
   
});

}
ngOnInit(){

  this.UserForme = this.fb.group({
    userName: ['', Validators.required ],
    password: ['', Validators.required ]
  });
  this.onChanges()
}
onChanges():void{

  this.UserForme.get('userName').valueChanges.subscribe(val => {
    this.usenameMessages = null;
  });
  this.UserForme.get('password').valueChanges.subscribe(val => {
    this.pwdMessages = null;
  });
  
}
goToPass(){
  this.router.navigate(['auth/Password']) ;


}
}

