import { messages } from '../../pages/extra-components/chat/messages';
import { UserAuth, UserData } from '../../@core/data/users';
import { Component, OnInit, OnChanges } from '@angular/core';
import { Router } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { AuthenticationService } from '../Auth.Service';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { NbThemeService, NbDialogService } from '@nebular/theme';
import { ShowcaseDialogComponent } from '../../pages/modal-overlays/dialog/showcase-dialog/showcase-dialog.component';

@Component({
    selector: 'nb-password',
    styleUrls: ['./password.component.scss'],
    templateUrl: './password.component.html',
})
export class PasswordComponent implements OnInit  {

  PassForme: FormGroup;
  usenameMessages:any;
  pwdMessages:any;
cofirmedPass:boolean=false;
cofirmePass:boolean=true;
  constructor(private router:Router,private http:HttpClient,private fb: FormBuilder,private authService:AuthenticationService,private dialogService:NbDialogService ){ 
    
    this.authService.isLogged=false;
  }
  changePassword(userData){

 this.http.post<any>('http://localhost:3000/rfq/login',userData).subscribe(result=>{
    if(result.token!=null)
   
 {if(this.PassForme.get('newpassword').value==this.PassForme.get('cofirmpassword').value){


userData['password']=this.PassForme.get('newpassword').value;

   this.http.put<any>('http://localhost:3000/rfq/users',userData).subscribe(result=>{
    this.router.navigate(['auth/login']);
    this.dialogService.open(ShowcaseDialogComponent, {
      context: {
        
        message:'Password changed successsfully ',
      },
    });

   });

 }
 
 }
 else if (result.message!=null)

 {this.usenameMessages=result.message;}
 else this.pwdMessages=result.pwdmessage;
   
});

}
ngOnInit(){

  this.PassForme = this.fb.group({
    userName: ['', Validators.required ],
    password: ['', Validators.required ],
    newpassword: ['', Validators.required ],
    cofirmpassword: ['', Validators.required ]

  });
  this.onChanges()
}
onChanges():void{
  this.PassForme.get('userName').valueChanges.subscribe(val => {
    this.usenameMessages = null;
  });
  this.PassForme.get('password').valueChanges.subscribe(val => {
    this.pwdMessages = null;
  });
  this.PassForme.get('cofirmpassword').valueChanges.subscribe(val => {
    if(this.PassForme.get('newpassword').value!=this.PassForme.get('cofirmpassword').value){
      this.cofirmePass=false; 
      this.cofirmedPass=false;  
    }
    else {this.cofirmedPass=true;
    this.cofirmePass=true;}
  });
}

}

